body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    height: 100vh;
    margin: 0;
    padding: 0;
    font-size: calc(5px + 2vmin);
    background-color: rgb(242, 247, 242);
}

#root .primary_color {
    color: rgb(40, 30, 182);
}

#root .primary_bg_color {
    background-color: rgb(40, 30, 182);
}

#root .sec_color {
    color: rgb(236, 190, 90);
}

#root .sec_bg_color {
    color: rgb(236, 190, 90);
}

#root .rm_hl,
#root .rm_hl:hover,
#root .rm_hl:active,
#root .rm_hl:visited {
    outline: none !important;
    box-shadow:none !important;
}

#root .submit_btn {
    background-color: rgb(48, 34, 238);
    color: white;
    border: 1px solid rgb(236, 190, 90);
}

#root .submit_btn:hover {
    background-color: rgb(40, 30, 182);
    color: whitesmoke;
}


